import { Block, BlockChildren } from '@ali/4ever-cangjie';
import type { ParagraphProperties, ParagraphData, ParagraphChildren } from '@ali/4ever-plugin-paragraph';
import { FoldableHeadingData } from './heading1';

export default class Heading3 extends Block<FoldableHeadingData, ParagraphChildren> {
  static TYPE = 'heading-3';

  static create<SD = ParagraphData, SN extends BlockChildren = ParagraphChildren>(
    properties: Heading3 | ParagraphProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`Heading3.create` does not accept string');
    }
    if (Heading3.isHeading3(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: Heading3.TYPE,
    });
  }

  static isHeading3(maybeHeading3: any): maybeHeading3 is Heading3 {
    return Block.isBlock(maybeHeading3) && maybeHeading3.type === Heading3.TYPE;
  }

}
